
*{margin: 0px; padding: 0px;}
h1, h2 {
  text-align: center;
  margin: 30px auto;
}

ul, li{
  list-style: none;
}
ul{
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}
ul li{
  width: 45%;
  margin: 10px auto;

}
ul li img{
  width: 100%;
}
label{
  display: inline-block;
  text-align: left;
  width: 30%;
}
input[type=text]{
  width:60%;
  height: 40px;
  margin: 10px 0px;
}
.createbtn{
  text-align: center;
}
.inputbox p{
  width: 100%;
  text-align: center;
}
input[type=button]{
  background-color: rgb(95, 0, 128);
  border: none;
  width: 100%;
  height: 30px;
  color:#fff;
  font-weight: bold;
}
input[type=button]:hover{
  background: #ccc;
}

.createbtn input{
  width: 60%;
  height: 40px;
  background: none;
  border: 1px solid rgb(95, 0, 128);
  color:#333;
  margin: 30px 0px;
}
.createbtn input:hover{
  background:  rgb(95, 0, 128);
  color:#fff;
}